import React from 'react';
import styled from 'styled-components';

const SVG = styled.svg`
    .camera {
        stroke-dasharray: 800;
        stroke-dashoffset: 800;
        animation: 3s camera infinite ease-in;
    }

    circle {
        stroke-dasharray: 300;
        stroke-dashoffset: 300;
        animation: 3s circle infinite cubic-bezier(1, 0, 0, 1);
    }

    .text {
        fill: none;
        stroke-width: 1px;
        stroke-dasharray: 100;
        stroke-dashoffset: 100;
        animation: 3s textappear infinite ease-in;
    }

    @keyframes circle {
        from {
            stroke-dashoffset: -300;
        }
        to {
            stroke-dashoffset: 0;
        }
    }

    @keyframes camera {
        from {
            stroke-dashoffset: 800;
        }
        to {
            stroke-dashoffset: 0;
        }
    }

    @keyframes textappear {
        from {
            stroke-dashoffset: 100;
        }
        to {
            stroke-dashoffset: 0;
        }
    }
`;
